.intro__container {
  background-image: url('../../img/index-background-mobile@1x.jpg');
  background-repeat: no-repeat;
  background-color: #ffffff;
  background-size: 100% 305px;
  width: 100%;
  margin: 0 auto;
  padding-top: 26px;
  padding-left: 20px;
  padding-right: 20px;

  @media(min-resolution: @retina-dppx) {
    background-image: url('../../img/index-background-mobile@2x.jpg');
  }
}

.intro__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: #ffffff;
  width: 280px;
  margin: 0 auto;
  margin-bottom: 26px;
  text-align: center;
}

.intro__description {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  width: 200px;
  margin: 0 auto;
  margin-bottom: 28px;
}

.intro__image {
  display: block;
  margin: 0 auto;
  margin-bottom: 3px;
}

.intro__link {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  display: block;
  text-align: center;
  margin: 0 auto;
  padding-top: 11px;
  padding-bottom: 11px;

  &:hover {
    background-color: #5eaa2f;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

@media (min-width: @tablet-width) {
  .intro__container {
    background-image: none;
    width: 768px;
    margin: 0 auto;
    padding-top: 29px;
    padding-left: 30px;
    padding-right: 30px;
    display: flex;
    flex-direction: column;
  }

  .intro__title {
    font-size: 60px;
    line-height: 60px;
    font-weight: 400;
    color: #111111;
    text-align: left;
    width: 425px;
    margin-bottom: 41px;
    margin-left: 88px;
  }

  .intro__description {
    font-size: 20px;
    line-height: 20px;
    font-weight: 400;
    color: #111111;
    text-align: left;
    width: 290px;
    margin-bottom: 48px;
    margin-left: 88px;
  }

  .intro__pic {
    order: 4;
    margin-top: -80px;
  }

  .intro__image {
    width: 709px;
    height: 609px;
    margin-bottom: 0;
  }

  .intro__link {
    font-size: 20px;
    line-height: 20px;
    margin-left: 88px;
    padding-top: 15px;
    padding-right: 27px;
    padding-bottom: 17px;
    padding-left: 27px;
    width: 245px;
    z-index: 2;
    order: 3;
  }
}

@media (min-width: @desktop-width) {
  .intro__container {
    width: 100%;
    max-width: 1440px;
    margin-top: -125px;
    padding-top: 223px;
    padding-bottom: 188px;
    background-image: url('../../img/index-background-desktop@1x.jpg');
    background-size: 720px 100%;
    background-position: right -45px top;
    position: relative;
    @media (min-resolution: @retina-dppx) {
      background-image: url('../../img/index-background-desktop@2x.jpg');
    }
  }

  .intro__title {
    width: 425px;
    margin-left: 115px;
  }

  .intro__description {
    margin-left: 115px;
    margin-bottom: 53px;
  }

  .intro__pic {
    position: absolute;
    left: 515px;
    top: 143px;
    margin-top: 0;
  }

  .intro__link {
    margin-left: 115px;
  }

  .intro__image {
    width: 552px;
    height: 499px;
    margin-bottom: 0;
  }
}
